
.search-bar {
    -webkit-appearance: none;
    border: none;
    outline: none;
    padding: 10px;
    width: 25vw;
    line-height: 30px;
    margin-top:10px ;
    margin-bottom: 30px;
    background-color:aliceblue;
    border-radius: 10px;
    transition: transform 0.25s ease-out;
}

.search-bar:hover{
    transform: scale(1.05);
} 

